
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

var top=0;

function goTop(ele){
var top=$(ele).offset().top//
$('html, body').animate({      //
scrollTop: top
}, 100);
}
export default{
components:{
Swiper,
Sticky,
Popup,
Cell,
Group,
Popup
},
data(){
return{
bannerList:[],
mtFiltrateVal:false,//
cinemas:[],//
hot_movie:[],//
area_list:{},//
areaName:"",
lat:"",//
lon:"",//
city_name:"",
isCity:false,
city_list:[],
}
},
created:function(){
var self=this;
this.$wechat.ready(function(){
self.mtAddress();
})
},
methods:{
getData:function(){
this.$http.get('/wemovie/index.php',{params:{lat:this.lat,lon:this.lon}}).then((res)=>{
if(res.data.code==200){
var data=res.data.data;
this.cinemas=data.cinemas;
this.hot_movie=data.hot_movie;
this.area_list=data.area_list;
this.city_name=data.city_name;
this.city_list=data.city_list;
this.bannerList=data.banner.map((item, index) => ({
url: '',
img: "http://www.juyoufuli.com/data/afficheimg/"+item.ad_code,
title:item.ad_name
}))

$('html, body').animate({      //
scrollTop: 0
}, 100);

console.log(res)
}else{
console.log(res)
alert(res.data.message)
}
})
},
mtAddress:function(){


this.areaName=""
var self=this;
this.$wechat.getLocation({
type: 'wgs84', // wgs84gpsopenLocation'gcj02'
success: function (res) {
console.log(res)
self.lat=res.latitude;
self.lon=res.longitude;
self.getData();
},
cancel: function (res) {
self.getData();
},
error:function(res){
self.getData();
},
fail:function(){
self.getData();
}
});
},
mtFiltrateArea:function(){
this.mtFiltrateVal=!this.mtFiltrateVal;
},
lookAll:function(){
this.$router.push({
name:"hotShowing",
})
},
filterArea:function(areaId,name){
this.mtFiltrateVal=!this.mtFiltrateVal;
this.$http.get("/wemovie/index.php?act=areaCinemas",{params:{districtid:areaId,lat:this.lat,lon:this.lon}}).then((res)=>{
var data=res.data.data;
if(res.data.code=200){
this.cinemas=data.cinemas;
this.areaName=name;
}
})
},
toMtDetail:function(movie_id){
this.$router.push({
name:"mtDetail",
params:{
movie_id:movie_id
}
})
},
toCinemaDetail:function(cinema_id){
this.$router.push({
name:"cinemaDetail",
query:{
cinemaid:cinema_id,
}
})
},

toOrderList:function(){
this.$router.push({
name:"orderList"
})
},

selectCity:function(){
this.isCity=!this.isCity;
},
changeCity:function(cityid){
this.$http.get('/wemovie/index.php',{params:{lat:this.lat,lon:this.lon,cityid:cityid}}).then((res)=>{
if(res.data.code==200){
var data=res.data.data;
this.cinemas=data.cinemas;
this.hot_movie=data.hot_movie;
this.area_list=data.area_list;
this.city_name=data.city_name;
this.city_list=data.city_list;
this.bannerList=data.banner.map((item, index) => ({
url: '',
img: "http://www.juyoufuli.com/data/afficheimg/"+item.ad_code,
title:item.ad_name
}));
this.isCity=!this.isCity;
console.log(res)
}else{
console.log(res)
alert(res.data.message)
}
})
}
},
watch:{
mtFiltrateVal(curVal,oldVal){
if(curVal==false){
$('body').removeClass('mt-add');//body
$(window).scrollTop(top);
$(".vux-sticky-box").removeClass("mt-fixed")
}else{
var winScrollTop=$(window).scrollTop();
if(winScrollTop<400){
top = 400;
$('body').css("top",-top+"px");//bodytop
}else{
top = $(window).scrollTop();//scrollTop
$('body').css("top",-top+"px");//bodytop
}
$(".vux-sticky-box").addClass("mt-fixed")
$('body').addClass('mt-add');//bodyposition:fixed;
}
}
}
}



